@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
:root {
  --white-color: #FAFFFD;
}

*, *::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}

.container {
  max-width: 1100px;
  height: 100%;
  padding: 0 40px;
  margin: 0 auto;
}

.flex {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
}
.CoverTopImage {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    transition: all 0.5s;
    overflow: hidden;
}

.CoverTopImage::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
}

.CoverTopImage .svg-shape {
    position: absolute;
    bottom: -60px;
    left: 0;
    width: 100%;
    height: 50px;
}

.CoverTopImage .svg-shape path {
    fill: var(--primary);
}

.CoverTopImage > .arrows {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 20px;
}

.CoverTopImage > .arrows span {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-weight: 500;
    font-size: 4rem;
    color: var(--white-color);
    cursor: pointer;
}

.CoverTopImage > .container .theme {
    position: absolute;
    top: 80px;
    left: 20px;
    background-color: rgba(255, 255, 255, 0.2);
    padding: 10px;
    border-radius: 5px;
}

.CoverTopImage > .container .theme h3 {
    text-align: center;
    color: var(--white-color);
    letter-spacing: 1px;
    font-weight: 500;
    text-shadow: 0 0 5px #000;
}

.CoverTopImage > .container .theme .colors {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 15px;
}

.CoverTopImage > .container .theme .colors span {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    cursor: pointer;
}

.CoverTopImage > .container .theme .colors span:first-child {
    background-color: #272727;
}

.CoverTopImage > .container .theme .colors span:nth-child(2) {
    background-color: #31493C;
}

.CoverTopImage > .container .theme .colors span:last-child {
    background-color: #414066;
}

.CoverTopImage > .container .text-box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: calc(50% - 60px);
    left: 50%;;
    transform: translate(-50%, calc(50% - 60px));
    width: 500px;
    z-index: 20;
}

.CoverTopImage > .container .text-box h1 {
    font-weight: 500;
    letter-spacing: 1px;
    text-align: center;
    width: 100%;
    color: var(--white-color);
    font-size: 2.1rem;
}

.CoverTopImage > .container .text-box h1 .name {
    color: var(--secondary);
}

.CoverTopImage > .container .text-box p {
    text-align: center;
    letter-spacing: 1px;
    color: var(--white-color);
    margin-top: 10px;
    font-size: 1.2rem;
}

.CoverTopImage > .container .text-box .links {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 20px;
}

.CoverTopImage > .container .text-box .links a {
    text-shadow: 0 0 5px #000;
    position: relative;
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    border: 1px solid var(--white-color);
    border-radius: 5px;
    padding: 5px 10px;
    color: var(--white-color);
    transition: all 0.5s ease-in-out;
    overflow: hidden;
    width: 150px;
}

.CoverTopImage > .container .text-box .links a::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: var(--secondary);
    transition: all 0.5s ease-in-out;
    z-index: -10;
}

.CoverTopImage > .container .text-box .links a:hover::after {
    left: 0;
}

.CoverTopImage-animation {
    opacity: 0;
}

@media (max-width: 700px) {
    .CoverTopImage .svg-shape {
        display: none;
    }

    .CoverTopImage > .container .text-box {
        width: 90%;
        position: absolute;
        top: 50%;
    }

    .CoverTopImage > .container .text-box h1 {
        font-size: 1.6rem;
    }

    .CoverTopImage > .container .text-box p {
        font-size: 0.9rem;
    }

    .CoverTopImage > .arrows span {
        font-size: 3rem;
    }

    .CoverTopImage > .arrows {
        position: absolute;
        top: 40%;
    }

    .CoverTopImage > .container .theme {
        width: 150px;
        padding: 5px;
    }

    .CoverTopImage > .container .theme .colors span {
        border-radius: 15px;
    }

    .CoverTopImage > .container .text-box .links a {
        width: 120px;
    }
}
.Projects {
    width: 100%;
    min-height: calc(100vh - 60px);
    background-color: var(--primary);
    padding: 50px 0;
    overflow: hidden;
}

.Projects h1 {
    letter-spacing: 1px;
    font-weight: 500;
    width: 100%;
    text-align: center;
    font-size: 2rem;
    color: var(--secondary);
}

.Projects > .container.flex {
    flex-wrap: wrap;
    margin-bottom: 40px;
    margin-top: 40px;
}

.Projects > .container .project-box {
    position: relative;
    margin: 20px;
    width: 250px;
    color: #fff;
    cursor: pointer;
}

.Projects > .container .project-box:hover .front-face {
    transform: rotateY(-180deg);
}

.Projects > .container .project-box:hover .back-face {
    transform: rotateY(0deg);
}

.Projects > .container .project-box .front-face {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 5px; 
    transition: all 0.5s ease-in-out;
}

.Projects > .container .project-box .back-face {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: rotateY(180deg);
    border-radius: 5px; 
    transition: all 0.5s ease-in-out;
}

.Projects > .container .project-box .image-box {
    width: 250px;
    height: 300px;
}

.Projects > .container .project-box .image-box img {
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.Projects > .container .project-box h2 {
    font-weight: 500;
    margin-top: 20px;
    letter-spacing: 1px;
    width: 100%;
    text-align: center;
    color: var(--secondary);
}

.Projects > .container .project-box p {
    margin-top: 20px;
    letter-spacing: 1px;
    width: 100%;
    text-align: center;
    color: var(--white-color);
}

.Projects > .container .project-box .back-face a {
    text-shadow: 0 0 5px #000;
    display: block;
    position: relative;
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    border: 1px solid var(--white-color);
    border-radius: 5px;
    padding: 5px 10px;
    color: var(--white-color);
    margin-top: 20px;
    transition: all 0.5s ease-in-out;
    overflow: hidden;
}

.Projects > .container .project-box .back-face a::after {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: var(--secondary);
    transition: all 0.5s ease-in-out;
    z-index: -10;
}

.Projects > .container .project-box .back-face a:hover::after {
    left: 0;
}

.projects-animation {
    opacity: 0;
}
.Header {
    position: fixed;
    top: 0;
    left: 0;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 100%;
    height: 60px;
    z-index: 1000;
    transition: all 0.5s;
}

.dark {
    background-color: var(--primary);
}

.light {
    background-color: var(--light-color);
}

.Header > .container nav {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Header > .container ul {
    list-style: none;
    height: 100%;
    justify-content: space-between;
    align-items: center;
}

.Header > .container ul.active {
    display: flex;
}

.Header > .container li {
    letter-spacing: 1px;
    cursor: pointer;
    margin-left: 20px;
    color: var(--white-color);
}

.Header > .container li:hover {
    color: var(--secondary);
}

.Header > .container li:first-child {
    margin: 0;
}

.Header > .container img {
    width: 100px;
    height: 60px;
    transform: scale(0.6);
    object-fit: contain;
    cursor: pointer;
}

.Header > .container .btn {
    cursor: pointer;
    z-index: 200;
    display: none;
}

.Header > .container .btn svg {
    font-size: 1.8rem;
}

.Header > .container .btn svg path {
    fill: var(--secondary);
}

@media (max-width: 600px) {
    .Header > .container {
        padding: 0 20px;
    }

    .Header > .container ul {
        display: none;
        justify-content: center;
        flex-direction: column;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: var(--primary);
    }

    .Header > .container ul li {
        margin: 0;
        margin-top: 20px;
        font-size: 1.5rem;
    }

    .Header > .container .btn {
        display: block;
    }
}
.Skills {
    width: 100%;
    min-height: calc(100vh - 60px);
    background-color: var(--primary);
    padding: 50px 0;
    overflow: hidden;
}

.Skills h1 {
    letter-spacing: 1px;
    font-weight: 500;
    width: 100%;
    text-align: center;
    font-size: 2rem;
    color: var(--secondary);
}

.Skills > .container.grid {
    grid-template-rows: repeat(2, 1fr);
    grid-gap:10px;
    gap:10px;
    margin-top: 40px;
    margin-bottom: 40px;
}

.Skills > .container > .database,
.Skills > .container > .server {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    background-color: var(--light-color);
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5) inset,
                0 0 5px rgba(0, 0, 0, 0.1) inset;
}

.Skills > .container > .frontend {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    grid-column: 1 / span 2;
    background-color: var(--light-color);
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5) inset,
                0 0 5px rgba(0, 0, 0, 0.1) inset
}

.Skills > .container > .database > svg path,
.Skills > .container > .server > svg path,
.Skills > .container > .frontend > svg path {
    fill: var(--white-color);
}

.Skills > .container > .database > svg,
.Skills > .container > .server > svg ,
.Skills > .container > .frontend > svg {
    font-size: 3rem;
}

.Skills > .container h2 {
    margin-top: 10px;
    color: var(--secondary);
    font-weight: 500;
    letter-spacing: 1px;
    font-size: 1.2rem;
}

.Skills > .container .icons-box {
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 10px;
}

.Skills > .container p {
    margin-bottom: 10px;
}

.Skills > .container .icon {
    justify-content: center;
    flex-direction: column;
    margin: 10px;
    border-radius: 5px;
    padding: 5px 10px;
    background-color: rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5),
                0 0 5px rgba(255, 255, 255, 0.1);
}

.Skills > .container .icon p {
    font-size: 1.1rem;
    letter-spacing: 1px;
}

.Skills > .container .icon svg {
    font-size: 1.8rem;
}

.Skills > .container .icons-box span {
    color: #8f8e8e;
}

.Skills > .container .database .icons-box svg {
    transform: rotate(45deg);
}

.Skills > .container .database .icons-box path {
    fill: #589636;
}

.Skills > .container .server .icons-box .icon:first-child path {
    fill: #3C873A;
}

.Skills > .container .server .icons-box .icon:last-child path {
    fill: #F0DB4F;
}

.Skills > .container .frontend .icons-box .icon:first-child path {
    fill: #F16529;
}
.Skills > .container .frontend .icons-box .icon:nth-child(2) path {
    fill: #8ec3eb;
}
.Skills > .container .frontend .icons-box .icon:nth-child(3) path {
    fill: #F0DB4F;
}
.Skills > .container .frontend .icons-box .icon:last-child path {
    fill: #61DBFB;
}

@media (max-width: 600px) {
    .Skills > .container.grid {
        grid-template-rows: 1fr;
        grid-template-columns: repeat(3, 1fr);
    }

    .Skills > .container > .frontend,
    .Skills > .container > .server,
    .Skills > .container > .database {
        grid-column: 1 / span 3;
    }
}

.skills-animation {
    opacity: 0;
}
.Footer {
    width: 100%;
    background-color: var(--light-color);
    overflow: hidden;
}

.Footer > .container .social-network.flex {
    padding: 50px 0;
    list-style: none;
    justify-content: space-around;
    flex-wrap: wrap;
}

.Footer > .container .social-network li {
    margin: 20px;
}

.Footer > .container .social-network li:nth-child(odd) {
    color: var(--white-color);
}

.Footer > .container .social-network li:nth-child(even) {
    color: var(--secondary);
}

.Footer > .container .social-network li a {
    font-size: 2rem;
    color: inherit;
}

.Footer > p {
    width: 100%;
    text-align: center;
    letter-spacing: 1px;
    color: var(--secondary);
    background-color: var(--primary);
    padding: 10px 0;
}

.footer-animation {
    opacity: 0;
}
.About {
    width: 100%;
    min-height: calc(100vh - 60px);
    background-color: var(--light-color);
    padding: 50px 0;
    overflow: hidden;
}

.About h1 {
    letter-spacing: 1px;
    font-weight: 500;
    width: 100%;
    text-align: center;
    font-size: 2rem;
    color: var(--secondary);
}

.About > .container {
    margin-bottom: 40px;
    margin-top: 40px;
}

.About > .container p {
    margin-top: 20px;
    letter-spacing: 1px;
    width: 100%;
    text-align: left;
    font-size: 1.3rem;
    color: var(--white-color);
    font-weight: 500;
    line-height: 1.4;
}

.about-animation {
    opacity: 0;
}
